<template>
  <div class="main-l">
    <div class="login">
      <div class="login-main">
        <div class="login-top">
          <h1>洁山口腔</h1>
          <el-form :model="admin" :rules="rules" ref="admin">
            <el-form-item prop="username">
              <el-input
                  size="normal"
                  type="text"
                  placeholder="请输入管理员名"
                  prefix-icon="el-icon-search"
                  v-model="admin.username">
              </el-input>
            </el-form-item>
            <el-form-item prop="password">
              <el-input
                  size="normal"
                  type="password"
                  placeholder="请输入密码"
                  prefix-icon="el-icon-search"
                  v-model="admin.password">
              </el-input>
            </el-form-item>
            <br>
            <el-button type="primary" size="normal" @click="login()">登录</el-button>
            <el-button type="danger" size="normal" style="margin-left: 10px">重置</el-button>
          </el-form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import request from "@/utils/request";

export default {
  name: "LoginView",
  data() {
    return {
      admin: {},
      rules: {
        username: [
          {required: true, message: '请输入管理员名', trigger: blur}
        ],
        password: [
          {required: true, message: '请选择密码', trigger: blur}
        ]
      },
    }
  },
  created() {
  },
  methods: {
    login() {
      this.$refs.admin.validate((valid) => {
        if (valid) {
          request.post("/admin", this.admin).then(res => {
            if (res.state === 0) {
              this.$message({
                message: '登录成功',
                type: 'success'
              });
              localStorage.setItem("admin", JSON.stringify(res.data));
              this.$router.push("/")
            } else {
              this.$message.error("管理员名或密码有误！");
            }
          })
        }
      });
    }
  }
}
</script>

<style scoped>
.main-l {
  background: url(@/assets/Z.jpeg) no-repeat;
  background-size: cover;
  padding: 70px 0px 70px 0px;
  min-height: 900px;
  font-family: 'Roboto', sans-serif;
  font-size: 100%;
}

.login {
  width: 40%;
  background: #fff;
  margin: 0 auto;
  min-height: 400px;
  padding: 10px;
}

.login-main {
  border: 2px solid #7397D1;
  min-height: 400px;
}

.login-top {
  padding: 45px 0px;
  text-align: center;
  position: relative;
}

.login-top h1 {
  font-size: 26px;
  font-weight: 500;
  color: #37588B;
  margin: 10px 0px 35px 0px;
}

.login-top .el-input {
  width: 60%;
  margin-bottom: 20px;
}
</style>